*{
    padding: 0;
    margin: 0;
}
.wrapper{
    width: 100%;
    height: 100vh;
    display: flex;
}
.top{
    width: 100%;
    background-color: #262626;
    height: 5vh;
    text-transform: lowercase;
    display: flex;
    justify-content:center;
    align-items: center;
    color: #f2f2f2;
    font-size: .8em;
}
.editor{
    width: 100%;
}
textarea{
    width: 100%;
    height: 94vh;
    resize: none;
    border: none;
}
.preview{
    width: 100%;
    display: flex;
    flex-direction: column;
    border-left: 1px solid #262626;
}
@media only screen and (max-width: 600px) {
    .wrapper{
        flex-direction: column;
        height: auto;
    }
  }